.table{
    width: 100%;
    border-collapse: collapse;
    background-color: #0003e1;
    color: #96daff;
    font-size: 20px;
    border-top: 3px double #96daff;
    border-bottom: 3px double #96daff;
    border-left: 5px double #96daff;
    border-right: 5px double #96daff;
}

.table_caption {
    margin-bottom: 25px;
    text-transform: uppercase;
    font-size: 30px;
    color: #ffd700;
}

th, td {
    border-left: 5px double #96daff;
    border-right: 5px double #96daff;
}

th {
    height: 50px;
    color: #ffd700;
}

td {
}